body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.content {
  margin: auto;
  width: 80%;
  text-align: center;
  padding: 250px 0;
  font-family: 'Berkshire Swash', cursive;
  background: white !important;
}

.content .title {
  font-size: 68px;
  margin-top: 16px;
  color: black;
}

.content .description {
  color: black;
  margin-top: 0px
}

.content-about {
  margin: auto;
  width: 60%;
  padding-top: 32px;
  padding-bottom: 32px;
  font-family: 'Lato', sans-serif;
}

.content-about .p {
  white-space: pre-wrap;
  font-size: 16px;
  line-height: 2;
  color: black;
  text-align: justify;
}

.logo {
  width: 40px;
  height: 40px;
  background: rgba(255, 255, 255, 0.2);
  margin: 16px 24px 16px 16px;
  float: center;
}

.bottom-layout {
  width: 100%;
  height: 10vh;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
}

.bottom-layout svg {
  padding: 0 12px;
  height: 2.5rem;
  width: auto;
}

#components-layout-demo-side .logo {
  height: 32px;
  background: rgba(255, 255, 255, 0.2);
  margin: 16px;
}

#rounded-line-green {
  border-radius: 5px;
  background: #1979D9; 
  width: 200px;
  height: 5px;  
  margin-bottom: 32px;
}